import { KeyboardReturnOutlined } from "@mui/icons-material";
import React, {useState} from "react";
import wordList from "../data/wordList.json";
import WordComponent from "./WordComponent";


const WordContainer = (props) => {


    let storedIndexString = window.localStorage.getItem("currentIndex");

    let storedIndex = {index:0};

    if (storedIndexString){
        storedIndex = JSON.parse(storedIndexString);
    }

    const [currentIndex, setCurrentIndex] = useState(storedIndex.index);

    const defaultWord = wordList.Words[currentIndex];

    const [currentWord, setCurrentWord] = useState(defaultWord);



    
    const max = wordList.Words.length;

    const setNewData = (index) => {
        let word = wordList.Words[index];
        setCurrentIndex(index);
        setCurrentWord(word);
        const storage = {
            index : index
        }
        window.localStorage.setItem("currentIndex",JSON.stringify(storage));

    };

    const nextCommand = () => {
        
        if (currentIndex + 1 > max-1){
            return;
        }
        let nextIndex = currentIndex+1;
        setNewData(nextIndex);

    };

    const prevCommand = () => {
        if (currentIndex === 0){
            return;
        }
        let nextIndex = currentIndex-1;
        setNewData(nextIndex);
    };

    const gotoCommand = (newIndex) => {
        
        if (newIndex < 0){
            return;
        }

        if (newIndex-1 > max-1){
            return;
        }

        setNewData(newIndex);


    }

    
    return (

        <WordComponent
            index = {currentIndex}
            word = {currentWord}
            nextCommand = {nextCommand}
            previousCommand={prevCommand}
            max= {max}
            gotoCommand = {gotoCommand}

                
        >

        </WordComponent>
    )



};

export default WordContainer;