
import React, { useState } from "react";
import Button from '@mui/material/Button';
import CameraIcon from '@mui/icons-material/PhotoCamera';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import GotoDialog from "./GotoDialog";

const WordComponent = ({index, word, nextCommand, previousCommand, max, gotoCommand}) =>{


    const [showMeaning, setShowMeaning] = useState(false);

    const [open, setOpen] = React.useState(false);

   

    const wordText = word.Word;

    const handleShowMeaning = (event) => {

        setShowMeaning(true);
        
    };

    const handleNext = (event) => {
    setShowMeaning(false);
       nextCommand();
        
    };

    const handlePrev = (event) => {
        setShowMeaning(false);
       previousCommand();
        
    };

    const handleGoto = () => {
        setOpen(true);

    };

    const gotoWord = (data) => {
        setOpen(false);
        console.log("In goto data is " + data);
        if (data === -1){
            return;
        }
        if (data - 1 < 0){
            return;
        }
        setShowMeaning(false);
        gotoCommand(data-1);
    }

    const renderMeanings2=() => {

        return (

        <Container  sx={{ py: 8 }} maxWidth="md" style={{
            backgroundColor: "#E7EBF0",
            paddingTop: "0"
          }}>
          {/* End hero unit */}
          <Grid container spacing={4}>
            {word.Meanings.map((value, index) => (
              <Grid item key={index} xs={12} sm={6} md={4}>
                <Card
                  sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                >
                  
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography>
                      {value}
                    </Typography>
                  </CardContent>
                 
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
        )

    };

    const renderMeanings=() => {


        return (
            <React.Fragment>
                <p>Meanings</p>
              
                {word.Meanings.map((value, index)=>{

                    return (
                <React.Fragment key={index}>
                    <Typography variant="h5" align="center" color="text.secondary" paragraph>
             {value}
            </Typography>
                 
                </React.Fragment>

                    )

                })};
             
            </React.Fragment>

        )
    }

    return (
        <React.Fragment>
        <Box
          sx={{
            bgcolor: 'background.paper',
            pt: 8,
            pb: 6,
          }}
        >
<Container maxWidth="sm">
            <Typography
              component="h1"
              variant="h2"
              align="center"
              color="text.primary"
              gutterBottom
            >
              {wordText}
            </Typography>
            
            <Stack
              sx={{ pt: 4 }}
              direction="row"
              spacing={2}
              justifyContent="center"
            >
              <Button variant="contained" onClick={handleShowMeaning}>Show Meaning</Button>
              <Button variant="outlined" onClick={handleNext}>Next</Button>
              <Button variant="outlined" onClick={handlePrev}>Previous</Button>
              <Button variant="outlined" onClick={handleGoto}>Goto</Button>
            </Stack>
          </Container>

        </Box>
        
        {showMeaning && 
                renderMeanings2()
            }
        <Box sx={{ bgcolor: 'background.paper', p: 6 }} component="footer">
        <Typography variant="h6" align="center" gutterBottom>
          Current Index is {index+1}
        </Typography>
        <Typography
          variant="subtitle1"
          align="center"
          color="text.secondary"
          component="p"
        >
          Total number of words is {max}
        </Typography>
        
      </Box>    
      <GotoDialog 
        max = {max}
        open = {open}  
        onClose = {gotoWord}   
      >

      </GotoDialog>
            
        </React.Fragment>
    )

};

export default WordComponent;